<template>
  <van-nav-bar
    left-text="站点首页"
    title="极速看片"
    fixed="true"
    @click-left="onClickLeft"
    class="nav-bar"
  />
  <!-- <van-image
    width="100%"
    height="50"
    src="https://img01.yzcdn.cn/vant/apple-1.jpg"
    class="top-banner"
  /> -->
  <div class="van-multi-ellipsis--l3 cell distance" type="flex">
    <!-- 使用 title 插槽来自定义标题 -->
    <span class="custom-title">{{ currentName }}</span>
  </div>
  <div class="detail">
    <div class="time">{{ currentDetail }}</div>
  </div>
  <div>
    <!-- <span v-if="true" class="banner-inline">
      <van-image
        width="100%"
        height="50"
        src="https://img01.yzcdn.cn/vant/apple-1.jpg"
      />
    </span> -->
    <video
      id="myVideo"
      class="video-js vjs-default-skin vjs-big-play-centered box"
      controls
      preload="auto"
      :poster="currentCover"
      playsinline="true"
    ></video>
  </div>
  <!-- <van-image
    width="100%"
    height="50"
    src="https://img01.yzcdn.cn/vant/apple-1.jpg"
    class="buttom-banner"
  /> -->
</template>

<script>
import { NavBar, Image } from "vant";
import videojs from "video.js";
import "videojs-contrib-hls";

export default {
  components: {
    [NavBar.name]: NavBar,
    [Image.name]: Image,
  },

  data() {
    return {
      videoUrl: "",
      currentCover: "",
      currentName: "无标题",
      currentDetail: "上传时间：无",
    };
  },

  computed: {
    poster: function () {
      return this.currentCover
        ? this.currentCover
        : "https://img01.yzcdn.cn/vant/apple-1.jpg";
    },
  },

  mounted() {
    document.documentElement.scrollTop = 0;
    let slug = this.$router.currentRoute._value.query.id;
    let data;

    if (window.films[slug] != undefined) {
      gtag("event", "share_link", {
        value: slug,
      });
      data = window.films[slug];
    } else if (localStorage[slug] != undefined) {
      gtag("event", "reload_local", {
        value: slug,
      });
      data = JSON.parse(localStorage[slug]);
    } else {
      gtag("event", "from_main_page", {
        value: slug,
      });
      this.getVedioBySlug(slug);
      return;
    }
    gtag("event", "video_play", {
      value: slug,
    });
    localStorage[slug] = JSON.stringify(data);

    this.videoUrl = new URL(data.url).href;
    this.currentName = data.title;
    this.currentDetail =
      "上传时间：" + new Date().toLocaleDateString().split("/").join("-");
    this.tag = data.tag;
    this.currentCover = data.cover;
    this.uploadTime = data.time;

    this.$nextTick(() => {
      this.getVideo();
    });
  },

  unmounted() {
    videojs("myVideo").dispose();
  },

  methods: {
    getVedioBySlug(slug) {
      console.log(window);
      this.$http.get(window.config.cdn + "output/" + slug).then((response) => {
        let data = response.data;
        localStorage[slug] = JSON.stringify(data);
        this.videoUrl = new URL(data.url).href;
        this.currentName = data.title;
        this.currentDetail =
          "上传时间：" + new Date().toLocaleDateString().split("/").join("-");
        this.tag = data.tag;
        this.currentCover = data.cover;
        this.uploadTime = data.time;

        this.$nextTick(() => {
          this.getVideo();
        });
      });
    },

    getVideo() {
      this.myVideo = videojs("myVideo", {
        bigPlayButton: true,
        textTrackDisplay: false,
        posterImage: false,
        errorDisplay: false,
      }).src({
        src: this.videoUrl,
        type: "application/x-mpegURL",
      });
    },

    onClickLeft() {
      gtag("event", "click_back", {})
      this.$router.push({
        path: "/",
      });
    },
  },
};
</script>

<style lang="less">
@nav-bar-title-font-size: 50;
.nav-bar {
  z-index: 3;
}
.banner-inline {
  z-index: 2;
  display: inline-block;
  position: absolute;
  width: 100%;
}
