  height: 24px;
  display: block;
}
.time {
  float: left;
  color: #999;
  padding-left: 10px;
}
.custom-title {
  font-size: 18pt;
  margin-left: 14px;
  margin-right: 14px;
  vertical-align: middle;
}
.cell {
  padding-top: 100px;
}
